import React from "react";
import SearchBar from "./SearchBar";
import youtube from "../api/youtube";
import VideoList from "./VideoList";
import FeaturedVideo from "./FeaturedVideo";
import logo from "../assets/logo.png";

class App extends React.Component {
  state = { videos: [], selectedVideo: null };

  componentDidMount() {
    this.onSearch("open source community");
  }

  onSearch = async (query) => {
    const response = await youtube.get("/search", {
      params: {
        q: query,
      },
    });
    this.setState({
      videos: response.data.items,
      selectedVideo: response.data.items[0],
    });
  };

  onVideoSelection = (video) => {
    this.setState({ selectedVideo: video });
  };

  render() {
    return (
      <div>
        <nav class="navbar">
          <img src={logo} style={{ width: 85 }} />
          <SearchBar onFormSubmit={this.onSearch} placeholder="Search Videos" />
        </nav>
        <hr />
        <div className="ui container">
          <div className="ui grid">
            <div className="ui row">
              <div className="eleven wide column">
                <FeaturedVideo video={this.state.selectedVideo} />
              </div>
              <div className="five wide column">
                <VideoList
                  onVideoSelection={this.onVideoSelection}
                  videos={this.state.videos}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
